import { Routes } from "@angular/router";
import { DashboardComponent } from "./dashboard/dashboard.component";
import { UsercomponentComponent } from "./usercomponent/usercomponent.component";
import { User2componentComponent } from "./user2component/user2component.component";
import { LoginpageComponent } from "./loginpage/loginpage.component";

export const Approute: Routes = [
  { path: "", pathMatch: "full", redirectTo: "/home" },
  {
    path: "home",
    component: DashboardComponent,
    data: { animation: "Home" }
  },
  {
    path: "user",
    component: UsercomponentComponent,
    data: { animation: "user" }
  },
  {
    path: "secondpage",
    component: User2componentComponent,
    data: { animation: "secondpage" }
  },
  {
    path: "loginpage",
    component: LoginpageComponent,
    data: { animation: "loginpage" }
  }
];
